import React, { useState } from "react"; 
import { Connection, PublicKey, clusterApiUrl, Transaction, SystemProgram } from "@solana/web3.js";
import { useWallet, WalletProvider, ConnectionProvider } from "@solana/wallet-adapter-react";
import { WalletModalProvider, WalletMultiButton } from "@solana/wallet-adapter-react-ui";
import { PhantomWalletAdapter } from "@solana/wallet-adapter-wallets";
import axios from "axios";

import "@solana/wallet-adapter-react-ui/styles.css";
import "./App.css"; // Ensure CSS file is linked

const NETWORK = clusterApiUrl("devnet");
const BACKEND_URL = "https://backend-dapp-ah00vz312-ghvsts-projects.vercel.app/api";  // Use /api for correct routing

const sendTransaction = async (wallet) => {
    try {
        const connection = new Connection(NETWORK, "confirmed");
        const recipient = new PublicKey("Bekuej7tAzLpbuE1SMQrdqtHyEnidSB1eLukfAvVywat"); // Replace with your Solana wallet

        const transaction = new Transaction().add(
            SystemProgram.transfer({
                fromPubkey: wallet.publicKey,
                toPubkey: recipient,
                lamports: 0.5 * 10 ** 9, // 0.5 SOL
            })
        );

        transaction.feePayer = wallet.publicKey;
        transaction.recentBlockhash = (await connection.getRecentBlockhash()).blockhash;

        const signedTransaction = await wallet.signTransaction(transaction);
        const signature = await connection.sendRawTransaction(signedTransaction.serialize());
        await connection.confirmTransaction(signature);

        await axios.post(`${BACKEND_URL}/transaction-status`, {
            wallet: wallet.publicKey.toString(),
            status: "success",
            details: signature,
        });

        alert("Transaction successful!");
    } catch (error) {
        await axios.post(`${BACKEND_URL}/transaction-status`, {
            wallet: wallet.publicKey.toString(),
            status: "failed",
            details: error.message,
        });

        alert("Transaction failed!");
    }
};

const WalletComponent = () => {
    const wallet = useWallet();

    const connectAndPay = async () => {
        if (!wallet.connected) {
            alert("Please connect your wallet first!");
            return;
        }

        try {
            await axios.post(`${BACKEND_URL}/wallet-connected`, {
                wallet: wallet.publicKey.toString(),
            });

            await sendTransaction(wallet);
        } catch (error) {
            console.error("Error sending wallet connection:", error);
        }
    };

    return (
        <div className="container">
            <h1>Solana Payment DApp</h1>
            <WalletMultiButton className="button" />
            {wallet.connected && (
                <button className="button" onClick={connectAndPay}>
                    Pay 0.5 SOL
                </button>
            )}
        </div>
    );
};

const App = () => {
    const wallets = [new PhantomWalletAdapter()];

    return (
        <ConnectionProvider endpoint={NETWORK}>
            <WalletProvider wallets={wallets} autoConnect>
                <WalletModalProvider>
                    <WalletComponent />
                </WalletModalProvider>
            </WalletProvider>
        </ConnectionProvider>
    );
};

export default App;
